@import url(https://cdn.rawgit.com/innks/NanumSquareRound/master/nanumsquareround.min.css);
#main-page{will-change:scroll-position;-webkit-scroll-snap-type:y mandatory;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory;overflow-y:scroll;height:100%;width:100%;position:fixed}#main-page>.scroll-line{scroll-snap-align:start;position:absolute}#main-page>.scroll-line.case0{top:180px}#main-page>.scroll-line.case1{top:450px}#main-page>.header{height:80px;background:white;color:#5C5C5C;font-size:20px;text-align:center;line-height:80px;font-family:'NanumSquareRound';font-weight:900;display:inline-block;width:100%}#main-page>.greeting{font-family:'NanumSquareRound';font-weight:900;width:100%;padding:28px 0px;color:#5C5C5C;text-align:center;font-size:20px;font-weight:900;display:inline-block}#main-page>.title{color:#000000;font-family:'nanumSquareRound';font-weight:900;font-size:20px;margin:0px 0px 20px 15px;display:inline-block}

#shadow-status{height:40px;scroll-snap-align:start}

#shadow-nav{height:84px}

#show-window{position:relative;height:280px}#show-window>.focus-bar{height:4px;position:relative}#show-window>.focus-bar .bar{height:100%;background:#FDB834;width:33%;position:absolute;border-radius:2px;transition:0.1s}#show-window>.focus-bar .bar.case0{left:calc(1% / 4)}#show-window>.focus-bar .bar.case1{left:calc(2% / 4 + 33%)}#show-window>.focus-bar .bar.case2{left:calc(3% / 4 + 66%)}#show-window>.btns{display:flex;position:relative;justify-content:space-around}#show-window>.btns button{background:white;width:33%;height:36px;font-size:14px;font-family:'nanumSquareRound';font-weight:700;color:#8E8E8E}#show-window>.btns button:focus{outline:none}#show-window>.btns button.selected{color:#000000}#show-window>.contents{position:relative}#show-window>.scroll-hider{background:white;position:absolute;height:20px;width:100%;z-index:1;top:250px}

.tools{transition:0.1s}.tools.wiggle{background:#fcad9422 !important}

#show-window-content{will-change:scroll-position;-webkit-scroll-snap-type:x mandatory;-ms-scroll-snap-type:x mandatory;scroll-snap-type:x mandatory;overflow-x:scroll;margin-top:30px;z-index:0;opacity:0;width:100%;height:200px;position:absolute;top:0px}#show-window-content.selected{opacity:1;z-index:1}#show-window-content>.boxs{display:flex;float:left}#show-window-content>.boxs .box{scroll-snap-align:center;height:180px;width:140px;margin:0px 5px}#show-window-content>.boxs .box img{position:absolute;width:140px;height:140px;border-radius:30px}#show-window-content>.boxs .box .photo{position:absolute;background:rgba(0,0,0,0.01);border-radius:30px;width:140px;height:140px}#show-window-content>.boxs .box .title{margin-top:156px;text-align:center;font-size:16px;font-family:'nanumSquareRound';font-weight:700}

#search-bar{padding:0px 15px;width:100%}#search-bar .contents{height:48px;border-radius:24px;background:#EFEFEF}#search-bar .contents input{border:none;color:#8E8E8E;font-size:14px;font-family:'nanumSquareRound';font-weight:700;background:rgba(0,0,0,0);line-height:48px;margin-left:48px;padding:0px}#search-bar .contents input::-webkit-input-placeholder{color:#8E8E8E}#search-bar .contents input:-ms-input-placeholder{color:#8E8E8E}#search-bar .contents input::-ms-input-placeholder{color:#8E8E8E}#search-bar .contents input::placeholder{color:#8E8E8E}#search-bar .contents .search-icon{position:absolute;left:30px;transform:translateY(16px)}

#search-bar1{padding:0px 15px;width:100%;position:fixed;transition:0.2s;height:44px;background:#FAC956;top:-4px}#search-bar1.case0{top:40px;z-index:2}#search-bar1.case1{top:40px;z-index:2;padding:0px 50px 0px 40px}#search-bar1.case1::before{position:absolute;content:"<";left:10px;color:white;font-weight:900;font-size:32px}#search-bar1.case1::after{position:absolute;content:"=";right:16px;top:-2px;color:white;font-weight:900;font-size:36px}#search-bar1 .contents{height:36px;border-radius:18px;background:#EFEFEF;margin-top:4px}#search-bar1 .contents input{border:none;color:#8E8E8E;font-size:14px;font-family:'nanumSquareRound';font-weight:700;background:rgba(0,0,0,0);line-height:36px;margin-left:18px;padding:0px}#search-bar1 .contents input::-webkit-input-placeholder{color:#8E8E8E}#search-bar1 .contents input:-ms-input-placeholder{color:#8E8E8E}#search-bar1 .contents input::-ms-input-placeholder{color:#8E8E8E}#search-bar1 .contents input::placeholder{color:#8E8E8E}

#common-list>.content{height:200px;margin:0px 15px 15px 15px;position:relative}#common-list>.content>.scroll-line{position:absolute;top:-100px;scroll-snap-align:start}#common-list>.content .img{width:75px;height:160px;position:absolute;top:10px;left:40px;overflow:hidden;display:flex;justify-content:center}#common-list>.content .img img{max-width:none;height:158px}#common-list>.content .profile{position:absolute;width:150px;height:120px;right:0px;top:30px}#common-list>.content .profile span{display:block;font-family:'nanumSquareRound';color:#5C5C5C;font-size:16px}#common-list>.content .profile span.name1{color:#000000;font-size:12px;font-weight:300;margin-bottom:10px}#common-list>.content .profile span.name2{font-weight:700;margin-bottom:5px}#common-list>.content .profile span.score{font-weight:500;margin-bottom:10px}#common-list>.content .profile span.price{font-size:12px;color:#7C7C7C;font-weight:300}#common-list>.content .panel{width:100%;height:50px;position:absolute;bottom:0;background:#f0f0f0dd;border-radius:0px 0px 15px 15px;display:flex;align-items:center}#common-list>.content .panel span{display:inline-block;font-family:'nanumSquareRound';color:#5C5C5C;font-weight:500}#common-list>.content .panel span.review{position:absolute;left:15px;font-size:12px;width:55%;white-space:nowrap;text-overflow:ellipsis;overflow:hidden}#common-list>.content .panel span.review::before{content:"리뷰";font-weight:700;font-size:14px;margin-right:15px}#common-list>.content .panel span.reviewer{position:absolute;right:100px;transform:translateX(100%);font-size:10px;display:flex;align-items:center}#common-list>.content .panel span.reviewer img{margin-right:10px}#common-list>.content .panel img{display:inline-block}#common-list>.content .card{display:block;position:absolute;height:100%;width:100%;border-radius:15px;background:#ffffff00;box-shadow:3px 3px 8px rgba(0,0,0,0.15)}#common-list>.content .heart{position:absolute;width:37px;height:37px;top:15px;left:15px;background:#FAC956;border-radius:50%;box-shadow:0px 1px 1px rgba(0,0,0,0.5)}#common-list>.content .heart img{margin-top:3px;max-width:none}


.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  margin: 15px;
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}


.container {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.container strong {
  font-size: 20px;
  line-height: 26px;
}

.container p {
  font-size: 16px;
  line-height: 22px;
  color: #8c8c8c;
  margin: 0;
}

.container a {
  text-decoration: none;
}

#red-carpet{position:fixed;height:100%;width:100%;z-index:1;background:#FFF7DD;transition:0.2s}#red-carpet.step0>.nameplate{transform:scale(0)}#red-carpet.step1{opacity:0}#red-carpet.step2{z-index:0}#red-carpet .nameplate{transition:0.2s;border:1px solid #707070;border-radius:30px;background:rgba(255,255,255,0.4);width:80px;height:218px;position:fixed;right:50px;bottom:100px;display:flex;align-items:center;justify-content:center}

/* Ionic Variables and Theming. For more info, please see:
http://ionicframework.com/docs/theming/ */

/** Ionic CSS Variables **/
:root {
  /** primary **/
  --ion-color-primary: #3880ff;
  --ion-color-primary-rgb: 56, 128, 255;
  --ion-color-primary-contrast: #ffffff;
  --ion-color-primary-contrast-rgb: 255, 255, 255;
  --ion-color-primary-shade: #3171e0;
  --ion-color-primary-tint: #4c8dff;

  /** secondary **/
  --ion-color-secondary: #3dc2ff;
  --ion-color-secondary-rgb: 61, 194, 255;
  --ion-color-secondary-contrast: #ffffff;
  --ion-color-secondary-contrast-rgb: 255, 255, 255;
  --ion-color-secondary-shade: #36abe0;
  --ion-color-secondary-tint: #50c8ff;

  /** tertiary **/
  --ion-color-tertiary: #5260ff;
  --ion-color-tertiary-rgb: 82, 96, 255;
  --ion-color-tertiary-contrast: #ffffff;
  --ion-color-tertiary-contrast-rgb: 255, 255, 255;
  --ion-color-tertiary-shade: #4854e0;
  --ion-color-tertiary-tint: #6370ff;

  /** success **/
  --ion-color-success: #2dd36f;
  --ion-color-success-rgb: 45, 211, 111;
  --ion-color-success-contrast: #ffffff;
  --ion-color-success-contrast-rgb: 255, 255, 255;
  --ion-color-success-shade: #28ba62;
  --ion-color-success-tint: #42d77d;

  /** warning **/
  --ion-color-warning: #ffc409;
  --ion-color-warning-rgb: 255, 196, 9;
  --ion-color-warning-contrast: #000000;
  --ion-color-warning-contrast-rgb: 0, 0, 0;
  --ion-color-warning-shade: #e0ac08;
  --ion-color-warning-tint: #ffca22;

  /** danger **/
  --ion-color-danger: #eb445a;
  --ion-color-danger-rgb: 235, 68, 90;
  --ion-color-danger-contrast: #ffffff;
  --ion-color-danger-contrast-rgb: 255, 255, 255;
  --ion-color-danger-shade: #cf3c4f;
  --ion-color-danger-tint: #ed576b;

  /** dark **/
  --ion-color-dark: #222428;
  --ion-color-dark-rgb: 34, 36, 40;
  --ion-color-dark-contrast: #ffffff;
  --ion-color-dark-contrast-rgb: 255, 255, 255;
  --ion-color-dark-shade: #1e2023;
  --ion-color-dark-tint: #383a3e;

  /** medium **/
  --ion-color-medium: #92949c;
  --ion-color-medium-rgb: 146, 148, 156;
  --ion-color-medium-contrast: #ffffff;
  --ion-color-medium-contrast-rgb: 255, 255, 255;
  --ion-color-medium-shade: #808289;
  --ion-color-medium-tint: #9d9fa6;

  /** light **/
  --ion-color-light: #f4f5f8;
  --ion-color-light-rgb: 244, 245, 248;
  --ion-color-light-contrast: #000000;
  --ion-color-light-contrast-rgb: 0, 0, 0;
  --ion-color-light-shade: #d7d8da;
  --ion-color-light-tint: #f5f6f9;
}

body
{
	background: #fac956 !important;
}

button:focus, input:focus
{
	outline: none;
}

.ion-color-tab
{
	--ion-color-base: rgba(0, 0, 0, 0);
	--ion-color-base-rgb: 255, 255, 255;
	--ion-color-contrast: rgba(0, 0, 0, 0.5);
	--ion-color-contrast-rgb: 0, 0, 0;
	--ion-color-shade: white;
	--ion-color-tint: white;
	--box-shadow: none;
	--border-radius: 50%;
}

ion-app ion-tab-bar,ion-app>.data-container{display:none}ion-app>.status-bar{top:0px;position:fixed;background:#FAC956;width:100%;height:40px}ion-app>.tab-bar{height:84px;width:calc(100% + 6px);border-radius:30px 30px 0px 0px;left:-3px;bottom:0px;border:none;position:fixed;background:#fac956;display:flex;justify-content:space-between;padding:0px 30px}ion-app>.tab-bar>ion-button{height:48px;width:48px;margin:4px 0px}ion-app>.tab-bar>ion-button.camera{background:white;border-radius:24px}ion-app>.tab-bar>ion-button.camera img{opacity:1}ion-app>.tab-bar>ion-button.selected>img{opacity:1}ion-app>.tab-bar>ion-button img{opacity:0.5;max-width:none}

