#red-carpet {
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 1;
    background: #FFF7DD;
    transition: 0.2s;
    &.step0 > .nameplate
    {
        transform: scale(0);
    }
    &.step1
    {
        opacity: 0;
    }
    &.step2
    {
        z-index: 0;
    }

    .nameplate {
        transition: 0.2s;
        border: 1px solid #707070;
        border-radius: 30px;
        background: rgba(255, 255, 255, 0.4);
        width: 80px;
        height: 218px;
        position: fixed;
        right: 50px;
        bottom: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
