ion-app
{
    ion-tab-bar, & > .data-container
    {
        display: none;
    }

    & > .status-bar
    {
        top: 0px;
        position: fixed;
        background: #FAC956;
        width: 100%;
        height: 40px;
    }
    
    & > .tab-bar
    {
        height: 84px;
        width: calc(100% + 6px);
        border-radius: 30px 30px 0px 0px;
        left: -3px;
        bottom: 0px;
        border: none;
        position: fixed;
        background: #fac956;
        display: flex;
        justify-content: space-between;
        padding: 0px 30px;
        
        & > ion-button
        {
            height: 48px;
            width: 48px;
            margin: 4px 0px;
            &.camera
            {
                background: white;
                border-radius: 24px;
                    
                img
                {
                    opacity: 1;
                }
            }
            &.selected > img
            {
                opacity: 1;
            }
    
            img
            {
                opacity: 0.5;
                max-width: none;
            }
        }
    }
}