@import url('https://cdn.rawgit.com/innks/NanumSquareRound/master/nanumsquareround.min.css');

body
{
	background: #fac956 !important;
}

button:focus, input:focus
{
	outline: none;
}

.ion-color-tab
{
	--ion-color-base: rgba(0, 0, 0, 0);
	--ion-color-base-rgb: 255, 255, 255;
	--ion-color-contrast: rgba(0, 0, 0, 0.5);
	--ion-color-contrast-rgb: 0, 0, 0;
	--ion-color-shade: white;
	--ion-color-tint: white;
	--box-shadow: none;
	--border-radius: 50%;
}
